import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';

import { NominaComponent } from './nomina/nomina.component';
import { PrestacionesComponent } from './prestaciones/prestaciones.component';
import { ServicioMedicoComponent } from './servicio-medico/servicio-medico.component';
import { EvaluacionesComponent } from './evaluaciones/evaluaciones.component';
import { CapacitacionComponent } from './capacitacion/capacitacion.component';

const routes: Routes = [
  {
    path: '',
    children: [
      { path: 'nomina', component: NominaComponent },
      { path: 'prestaciones-adicionales', component: PrestacionesComponent },
      { path: 'servicio-medico', component: ServicioMedicoComponent },
      { path: 'evaluaciones', component: EvaluacionesComponent },
      { path: 'capacitacion', component: CapacitacionComponent },
    ],
  },
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class RecursosHumanosRoutingModule {}
